/**
 * All of the CSS for your public-facing functionality should be
 * included in this file.
 */
.docs-info-header .currently-viewing .icon {
  margin: 0 2px 0 4px;
  vertical-align: middle;
}
#buddypress #bp-docs-all-docs li {
  display: inline-block;
  margin: 10px 10px 0 0;
  background: none;
}
#buddypress #bp-create-doc-button {
  margin-right: 0;
  margin-bottom: 1em;
  padding-top: 0.4em;
  padding-bottom: 0.4em;
  font-weight: normal;
  font-size: 14px;
  font-size: 1rem;
}
#buddypress .item-list-tabs #bp-create-doc-button {
  margin-top: 0;
}
#buddypress .bp-docs-attachment-drawer h4 {
  font-weight: normal;
}
#buddypress .bp-docs-attachment-drawer > ul > li {
  margin-bottom: 0;
}
#buddypress table.doctable {
  border: 1px solid #EEEEEE;
}
#buddypress table.doctable tr:nth-child(even) {
  background-color: #fbfbfb;
}
#buddypress table.doctable tr th {
  background-color: #EAEAEA;
  font-size: 14px;
  font-size: 1rem;
  font-weight: normal;
}
#buddypress table.doctable tr th a {
  text-decoration: none;
}
#buddypress table.doctable .title-cell {
  width: 30%;
}
#buddypress table.doctable .title-cell a {
  display: inline-block;
  font-size: 14px;
  font-size: 1rem;
  line-height: 1.4;
  margin-bottom: 0.5em;
}
#buddypress table.doctable .title-cell .row-actions a {
  font-size: 12.6px;
  font-size: 0.9rem;
  margin-bottom: 0;
}
#buddypress table.doctable .attachment-clip-cell {
  width: 4%;
}
#buddypress table.doctable .author-cell,
#buddypress table.doctable .created-date-cell,
#buddypress table.doctable .edited-date-cell {
  text-align: center;
  width: 13%;
}
#buddypress table.doctable .groups-cell {
  padding-left: 10px;
  width: 13%;
}
#buddypress table.doctable .groups-cell ul {
  list-style-type: none;
  padding-left: 0;
}
#buddypress table.doctable .groups-cell ul li {
  margin-bottom: .5em;
  margin-left: 0;
}
#buddypress table.doctable .groups-cell ul li a {
  vertical-align: middle;
  font-size: .85em;
}
#buddypress table.doctable .groups-cell ul li img.avatar {
  display: block;
}
.bp-create-doc-button-nav-container {
  float: right;
}
#buddypress #bp-docs-all-docs .mrad_create_doc_options_list li {
  margin: 0;
  width: 100%;
}
.item-list-tabs #bp-docs-all-docs .bp-create-doc-button-nav-container ul {
  border-bottom: 0;
  padding-bottom: 0;
}
.bp-create-doc-button-nav-container .accessible-menu .sub-nav {
  width: 17em;
}
.bp-create-doc-button-nav-container .accessible-menu .sub-nav:after,
.bp-create-doc-button-nav-container .accessible-menu .sub-nav:before {
  left: 55%;
}
#buddypress #bp-create-doc-button {
  padding-right: 26px;
}
#subnav .bp-create-doc-button-nav-container .accessible-menu .sub-nav .sub-nav-group li {
  width: 100%;
  margin-right: 0;
}
#subnav .bp-create-doc-button-nav-container .accessible-menu .sub-nav .sub-nav-group a {
  padding: 0 0.6em;
  display: inline-block;
  vertical-align: middle;
  width: 93%;
}
.bp-create-doc-button-nav-container .icon {
  display: inline-block;
  vertical-align: middle;
  background-color: #FFF;
  border-radius: 0;
  margin-right: .6em;
  padding: 0;
}
/* Undo some item-list-tab styling */
.buddypress .item-list-tabs .bp-create-doc-button-nav-container ul li a span.icon {
  background-color: inherit;
  border-radius: 0;
  font-size: 100%;
  margin-left: 0;
  padding: 0;
}
.doc-content .cc-map-widget img {
  margin: 0;
}
.doc-content .map-widget-data {
  display: none;
}
.doc-content .report-link,
.doc-content .map-link {
  margin-bottom: 1.2em;
}
.doc-content .reportx24,
.doc-content .mapx24 {
  display: inline-block;
  vertical-align: middle;
  margin-right: .4em;
  margin-top: 0;
}
#subnav.item-list-tabs .bp-create-doc-button-nav-container ul {
  border-bottom: 0;
}
.docs-filter-section ul.no-bullets {
  margin-bottom: 0;
}
.mrad-types-filter-list .icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: .8em;
}
.docs-filter-section .category-links a {
  background-color: #2cbdda;
  border-radius: 12px;
  color: #fff;
  display: inline-block;
  font-size: 0.9rem;
  line-height: 1.8;
  margin-bottom: 0.6rem;
  padding: 0 0.8em;
  transition: all 0.3s ease 0s;
}
.docs-filter-section .category-links a:visited {
  color: #fff;
}
.docs-filter-section .category-links a:hover {
  background-color: #008eaa;
  color: #fff;
}
/* Adjustments to the document table view*/
.doctable footer.entry-meta {
  margin-top: 0;
}
.doctable .doc-excerpt p {
  margin-bottom: .5em;
}
#buddypress table.doctable .title-cell footer.entry-meta .category-links a {
  font-size: .9rem;
  line-height: 1.8;
  background-color: white;
  border: 1px solid #ccc;
  color: #008EAA;
  transition: all .3s ease;
}
#buddypress table.doctable .title-cell footer.entry-meta .category-links a:hover {
  color: white;
  background-color: #008EAA;
  border-color: #008EAA;
}
.doctable .icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.3em;
}
/* Override the tendency of the title to bump down en masse. */
.site-content #buddypress table.doctable .title-cell a {
  display: inline;
}
/*Control the breadcrumb display*/
.bp-docs.buddypress .site-content .directory-title {
  font-size: 1rem;
  margin-top: 0;
}
.post-type-archive-bp_doc .directory-title {
  display: none;
}
.type-bp_doc .doc-tabs ul li {
  margin-bottom: 0;
  margin-left: .4em;
  padding-bottom: 0;
}
.type-bp_doc .doc-tabs ul li:first-of-type {
  margin-left: 0;
}
.type-bp_doc .doc-tabs ul li a {
  float: none;
  color: #008eaa;
  margin-left: 0;
}
.docs-list .doc-title {
  margin: 0 0 0 -28px;
}
.doc-title .icon {
  display: inline-block;
  vertical-align: middle;
  margin-right: 0.3em;
}
.docs-list .doc-excerpt p {
  margin-bottom: .5em;
}
.docs-list footer.entry-meta {
  margin-top: 0;
}
.list-item-breadcrumb {
  font-size: 11.200000000000001px;
  font-size: 0.8rem;
}
.ie7 .bp-create-doc-button-nav-container,
.ie8 .bp-create-doc-button-nav-container {
  float: right;
}
